import React from 'react';
import PropTypes from 'prop-types';
import ScrollToNext from '@components/ScrollToNext';
import './style.scss';
import Slide from 'react-reveal/Slide';
import Typing from 'react-typing-animation';
import Button from "@material-ui/core/Button";

const AboutPage = (props, context) => {
  const {
    theme: { colorPrimary, colorHighlight, bgPrimary, textPrimary }
  } = context;

  return (
    <div className="about-page" style={{ backgroundColor: bgPrimary }}>
      <style jsx="true">
        {`
          .highlight {
            background-color: ${colorHighlight};
          }
          ::selection {
            background-color: ${colorHighlight};
          }
        `}
      </style>
      <div className="content-grid">
      <Slide right>
        <h1 style={{ color: colorPrimary }}>About</h1>
      </Slide>
        <div className="about-wrapper">
          <div className="about-content" style={{ color: textPrimary }}>
            <p>
              Passionné d 'informatique depuis les années 90, je me suis formé pour approfondir mes connaissances et compétences dans le langage JavaScript :  <span className="highlight">React.js / Node.js / React.native</span> dans une formation intensive "La Capsule bootcamp" à Lyon.
            </p>
            <p>
              En reconversion professionnelle, j'ai travaillé dans l'hotellerie de luxe durant 15 années dans le domaine du Bar, ayant le goût pour la rigueur et l'excellence. {' '}
              Des établissements comme : {' '}
              <span className="highlight">Hotel Martinez Cannes, Mandarin Oriental Paris / Genève et le Royal Mansour Marrakech</span>.
              
            </p>
            <p>
                        A la recherche d'une entreprise pour une 1er expérience en tant que developpeur web react.js / react.native <span className="highlight">à Lyon et Paris</span>.
                        Passionné et curieux, j'aime découvrir et tester de nouveaux concepts, de nouvelles technologie, voulant me perfectionner sur la stack que j'utilise actuellement : <div> 
                        <Typing speed={200}><i>React-Redux, Node.js, React.native, Express.js, MongoDB.</i></Typing></div>
            </p>
            <p>
              Actuellement disponible pour des futurs entretiens, Lyon et Paris.
            </p>
            <p className="text-emoji" style={{ color: colorPrimary }}>
                              <Button variant="outlined" size="medium" color="primary"  target="_blank" style={{marginRight: 20}} href="https://drive.google.com/open?id=1Ip4ExqrD7fG1JwnU_MQZOdTWFzPBqZi4" >
                                  Mon CV
                            </Button>
                            <Button variant="outlined" size="medium" color="primary"  target="_blank" href="https://drive.google.com/open?id=0BwFfsH91yIATWC1oU2xLSXRlclRsaFl2angtV3lmTkpBay1v" >
                              Certificat La Capsule
                            </Button>
              </p>
                            <Typing>
                                    PS : Merci à Noël, Benjamin et Franck
                            <Typing.Backspace delay={1500} count={6} />
                                    Alexis @ La Capsule coding bootcamp
                            </Typing>
          </div>
        </div>
      </div>
      <ScrollToNext pageSelector=".portfolio-page" />
    </div>
  );
};

AboutPage.contextTypes = {
  theme: PropTypes.any
};

export default AboutPage;



